<template>
   <div id="wrapper">
    <component :is="model.name" v-bind="model.props"></component>
    <button @click="change">change</button>
    <hr>
    <component 
      v-for="(comp, index) in list" 
      :is="comp" 
      :key="index"
      v-bind="dyn[comp].props"
      ></component>
  </div>
</template>

<script>
import hero from '~/components/hero'
import paragraph from '~/components/paragraph'
export default {
  name:'dynamicpage',
  components: {
    hero,
    paragraph
  },
  data(){
    return {
      val: false,
      heroP : {
        name: 'hero',
        props: {
          title: 'dynamic title',
          text: 'dynamoc text...'
        }
      },
      paragraphP : {
        name: 'paragraph',
        props: {

        }
      },
      list: ['hero', 'hero', 'paragraph'],
      dyn: {
        hero : {
          name: 'hero',
          props: {
            title: 'dynamic title',
            text: 'dynamoc text...'
          }
        },
        paragraph : {
          name: 'paragraph',
          props: {}
        }
      }
    }
  },
  computed: {
    model () {
      return this.val ? this.heroP : this.paragraphP
    }
  },
  methods: {
    change() {
      this.val = !this.val
    }
  }
}
</script>

